import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Categoria_ishikawa } from '../../../modelo/Categoria_ishikawa'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'categoria_ishikawa-dados-edit',   templateUrl: 'categoria_ishikawa_dadosEdit.component.html' }) export class Categoria_ishikawaDadosEditComponent implements OnInit {    categoria_ishikawa: Categoria_ishikawa;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.categoria_ishikawa.Categoria_ishikawa";                 let id = +params['id'];         this.categoria_ishikawa = new Categoria_ishikawa();         this.categoria_ishikawa.cd_categoria_ishikawa = id;         o.objetoJson = this.categoria_ishikawa;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a categoria_ishikawa. ';       }      this.categoria_ishikawa = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


